import { BaseResponse } from "./api";
import request from "./base";
interface replyItem {
    id: number;
    article_id: number;
    user_id: number;
    username: string;
    catagory: "article" | "list";
    time: string;
    content: string;
    love: number;
    replyTo: number;
    commentId: number;
}
interface baseComment {
    id: number;
    username: string;
    time: string;
    content: string;
    love: number;
    reply: number;
    replyList?: replyItem[];
}

interface ArticleComment extends baseComment {
    article_id: number;
}
interface ListReplyBody {
    username: string;
    email: string;
    replyTo: number;
    commentId: number;
    content: string;
}
const getArticleComment = (id: number): Promise<BaseResponse<ArticleComment[]>> => request.get("/comment/article", {
    params: {
        id
    }
})
const addArticleComment = (
    article_id: number,
    content: string,
    username: string,
    email: string
)
    : Promise<BaseResponse<null>> => request.post("/comment/article/add", {
        data: {
            article_id, content,
            username, email
        }
    })
const addArticleReply = (
    article_id: number,
    email: string,
    username: string,
    content: string,
    replyTo: number,
    commentId: number)
    : Promise<BaseResponse<null>> => request.post("/reply/article/add", {
        data: {
            article_id, email, username, content, replyTo, commentId
        }
    })
const updateLove = (catagory: "article" | "comment" | "reply", type: "add" | "minus", id: number) => {
    let url: string;
    switch (catagory) {
        case "article": {
            url = "/article/update/love";
            break;
        }
        case "comment": {
            url = "/comment/love";
            break;
        }
        case "reply": {
            url = "/reply/love";
            break;
        }
    }
    return request.get(url, {
        params: {
            type, id
        }
    })
}
const getListComment = (): Promise<BaseResponse<baseComment[]>> => request.get("/comment");
const addListReply = (data: ListReplyBody): Promise<BaseResponse<null>> => request.post("/reply/list", {
    data
})
const addListComment = (data: { username: string, email: string, content: string }): Promise<BaseResponse<null>> => request.post("/comment/list", {
    data
})
const commentApi = {
    updateLove,
    addArticleReply,
    addArticleComment,
    getArticleComment,
    getListComment,
    addListReply,
    addListComment
}
export default commentApi
export { ArticleComment, replyItem, baseComment }