import request from "./base";

interface ArticleItem {
    id: number;
    type: string;
    title: string;
    article_info: string;
    article_content: string;
    time: string;
    view: number;
    love: number;
    catagory: string;
    catalogue: string
}
interface RegisterPayload {
    password: string;
    username: string;
    email: string;
}
interface LoginPayload {
    email: string;
    password: string;
}
interface BaseResponse<T> {
    message: string;
    statusCode: number;
    data: T
}
interface LoginRes {
    message: string;
    id: number;
    username: string;
    article_like: string[];
    comment_like: string[];
    reply_like: string[]
}
const getArticle = (id: number) => request.get("/article", {
    params: {
        id
    }
})
const getArticleList = (): Promise<BaseResponse<ArticleItem[]>> => request.get("/article");
const register = (data: RegisterPayload): Promise<BaseResponse<null>> =>
    request.post("/user/register", { data });
const login = (data: LoginPayload)
    : Promise<BaseResponse<LoginRes>> =>
    request.post("/user/login", {
        data
    })
const updateView = (id: number): Promise<BaseResponse<any>> =>
    request.get("/article/update/view", {
        params: {
            id
        }
    })
const updateArticleLike = (type: "add" | "minus", id: number): Promise<BaseResponse<any>> =>
    request.get("/article/update/love", {
        params: {
            type, id
        }
    })

const api = {
    getArticle,
    getArticleList,
    register,
    login,
    updateView,
    updateArticleLike
}
export default api
export { ArticleItem as ArticleItemProps, BaseResponse,LoginRes }